import React from 'react'

function Contact() {
    return (
        <div className="h-100vh contact">
            <div className="contact-in">
                <div className="contact-head">
                    Contact me
                </div>
                <div className="contact-form">
                    <form action="">
                        <div className="row">
                            <div className="contact-title">name</div>
                            <input className="text-input" type="text" placeholder='Van gogh' />
                        </div>
                        <div className="row">
                            <div className="contact-title">email</div>
                            <input className="text-input" type="text" placeholder='CreativeMf@gmail.com' />
                        </div>
                        <div className="row">
                            <div className="contact-title">Message</div>
                            <input className="text-input" type="text" placeholder="Hi bro let's do some collab I open on Wednesday and Tuesday" />
                        </div>
                        <div className="send-row">
                            <input className="send" type="submit" value="send" />
                        </div>
                    </form>
                </div>
            </div>
        </div>
    )
}

export default Contact
